import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { HttpService } from '../../api/http.service'
import { DomSanitizer } from '@angular/platform-browser';
import { PdfViewerModule } from 'ng2-pdf-viewer';
import { DocumentViewer, DocumentViewerOptions } from '@ionic-native/document-viewer/ngx';
import { ModalController } from '@ionic/angular';
import { ImageModalPage } from '../image-modal/image-modal.page';

@Component({
  selector: 'app-village-map',
  templateUrl: './village-map.page.html',
  styleUrls: ['./village-map.page.scss'],
  providers: [HttpService]
})

export class VillageMapPage implements OnInit {
  currentColor:any;
  theme:any;
  response:any;
  VillageMap:any;
  data:any;
  summerMap:any;
  winterMap:any;
  safeUrl:any;
  pdfSrc: string = '';
  constructor(private modalController: ModalController,private document: DocumentViewer,public router: Router,public shared: HttpService,public sanitizer: DomSanitizer) { 
    this.currentColor = 'light';
    this.theme = this.router.getCurrentNavigation().extras.state.theme;
  }

  ngOnInit() {
  }


  ionViewDidEnter() {
    if (navigator.onLine) {
            this.shared.ServiceCallVillage.ServiceCallRequestVillage().then((res) => { 
              // debugger
              this.VillageMap=res;
              this.data=this.VillageMap.content.posts[0];
    
              if (this.theme=='winter'){
                this.winterMap=this.data.wmpdf_files[0];
                console.log(this.winterMap)
              }else{
                this.winterMap=this.data.smpdf_files[0];
                console.log(this.winterMap)
              }
              
              
              }, err => {
               
              })
    
          }else{
            this.shared.displayNetworkUpdate('No Internet Connection')
          }
        
      }

      

      // getTrustedUrl(url:any){ 
      //   this.safeUrl = this.sanitizer.bypassSecurityTrustResourceUrl(url);
      //  }

       onLoad(){
      //  this.shared.hideLoading()
      }


      openPreview() {
        this.modalController.create({
          component: ImageModalPage,
          componentProps: {
            img: this.winterMap
          }
        }).then(modal => {
          modal.present();
        });
      }


}
